body {
  font-size: 62.5%;
}
main {
  max-width: 960px;
  margin: 1% auto;
  display: grid;
  grid-template-rows: 12% 72% 12%;
  grid-row-gap:  20px;
}

section {
  background: rgb(0,0,55,.1);
  display: -ms-flexbox;
  display: flex;
  padding: 0px 10px 10px;
  -ms-flex-direction: column;
      flex-direction: column;  
  -ms-flex-align: center;  
      align-items: center;
  border-radius: 3px;
}

h1{
  font-size: 2em;
}

section textarea{
  width: 100%;
  min-height: 200px;
  font-size: 1.8em;
  resize: none;
  border-color: #cecece;
  border-radius: 3px;
  font-family: sans-serif;
}

p{
  font-size: 1.4em;
  padding: 0 1em;
  text-align: left;
}

header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}


.faq{
  background: white;
  border-radius:50%;
  text-align: center;
  font-weight: bold;
  min-width: 25px;
  min-height: 25px;
  font-size: 1rem;
  line-height: 25px;
}


